<template src="./MusicPlayer.html"></template>
<style src="./MusicPlayer.css"></style>
<script>
    export default {
        name: 'MusicPlayer',
        data() {
            return {
                currentTrack: false,
            }
        },
        props: ['tracks'], // –ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–µ –Ω–∞–º —Ç—Ä–µ–∫–∏
        computed: {
            preparedTracks() {
                return this.tracks.map((track) => ({
                    id: track.id || Math.random().toString(36).substr(2, 9),
                    name: track.name,
                    url: URL.createObjectURL(track) // –°–æ–∑–¥–∞–µ–º URL –¥–ª—è –≤–æ—Å–ø—Ä–æ–∏–∑–≤–µ–¥–µ–Ω–∏—è
                }));
            },
        },
        methods: {
            SelectedTrack(track) {
                if (this.currentTrack !== track) {   // –ø—Ä–æ–≤–µ—Ä—è–µ–º, –∏–∑–º–µ–Ω–∏–ª—Å—è –ª–∏ –≤—ã–±–æ—Ä
                    this.currentTrack = track;
                    this.$emit("selected-track", track);
                }
            }
        }
    };
</script>