<template src="./MusicUploader.html"></template>
<style src="./MusicUploader.css"></style>
<script>
    // import { musicMetadata } from 'music-metadata';
    import { parseBlob } from 'music-metadata';
    
    export default {
        name: 'MusicUploader',
        data() {
            return {
                PlayList: false,
            }
        },
        // components: {
        //     musicMetadata
        // },
        methods: {
            async handleFileSelect(event) {
                const files = event.target.files;
                let validFiles = [];
        
                // –ü—Ä–æ—Ö–æ–¥–∏–º –ø–æ –∫–∞–∂–¥–æ–º—É —Ñ–∞–π–ª—É –∏ –ø—Ä–æ–≤–µ—Ä—è–µ–º –µ–≥–æ —Ç–∏–ø
                for (let file of files) {
                    if (!this.isAudio(file)) {
                        console.log(`–§–∞–π–ª ${file.name} –Ω–µ —è–≤–ª—è–µ—Ç—Å—è –∞—É–¥–∏–æ—Ñ–∞–π–ª–æ–º.`);
                        continue; // –ü–µ—Ä–µ—Ö–æ–¥–∏–º –∫ —Å–ª–µ–¥—É—é—â–µ–º—É —Ñ–∞–π–ª—É
                    }

                    
                    try {
                        const blob = file.slice() 
                        let parsedData = await parseBlob(blob); // üëà –ú–µ—Ç–æ–¥ parse –æ—Å—Ç–∞–ª—Å—è –ø—Ä–µ–∂–Ω–∏–º
                        
                        // console.log("parsedData", parsedData);
                        file.common = parsedData.common;
                        
                        
                    } catch (error) {
                        console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Ä–∞–∑–±–æ—Ä–µ —Ñ–∞–π–ª–∞:', error)
                    }
                    
                    validFiles.push(file); // –î–æ–±–∞–≤–ª—è–µ–º –ø–æ–¥—Ö–æ–¥—è—â–∏–π —Ñ–∞–π–ª –≤ –º–∞—Å—Å–∏–≤
                }

                // –ï—Å–ª–∏ –±—ã–ª–∏ –≤—ã–±—Ä–∞–Ω—ã –ø—Ä–∞–≤–∏–ª—å–Ω—ã–µ —Ñ–∞–π–ª—ã, –æ—Ç–ø—Ä–∞–≤–ª—è–µ–º –∏—Ö –≤–≤–µ—Ä—Ö
                if (validFiles.length > 0) {
                    this.$emit('files-selected', validFiles);
                    this.PlayList = true;
                } else {
                    alert("–ù–µ –±—ã–ª–æ –≤—ã–±—Ä–∞–Ω–æ –Ω–∏ –æ–¥–Ω–æ–≥–æ –∞—É–¥–∏–æ—Ñ–∞–π–ª–∞.");
                }
            },
            // –ü—Ä–æ–≤–µ—Ä—è–µ–º MIME-type —Ñ–∞–π–ª–∞ –Ω–∞ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤–∏–µ —Ç–∏–ø—É audio
            isAudio(file) {
                return new RegExp("^audio/", "i").test(file.type);
            }
        }
    }
</script>